import Vue from 'vue'
import Router from 'vue-router'
import Login from './components/Login'
import Index from './components/index/Index'
import menu1 from './components/index/index-com/menu1'
import menu2 from './components/index/index-com/menu2'
import indexIn from './components/index/index-com/index-in'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/index',
      name: 'Index',
      component: Index,
      redirect: '/index/index-in',
      children: [
        {
          path: '/index/index-in',
          name: 'indexIn',
          component: indexIn
        },
        {
          path: '/index/menu1',
          name: 'menu1',
          component: menu1
        },
        {
          path: '/index/menu2',
          name: 'menu2',
          component: menu2
        }
      ]
    }
    
  ]
})
